.header {
  background-color: #4CB8B3;
}
@media (min-width: 768px) {
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.5);
    transition: 0.3s;
    top: 0;
  }
}

.contenido-header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .contenido-header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header-title {
  color: #ffffff;
  text-align: center;
}

.navegacion-principal {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal {
    flex-direction: row;
    gap: 2rem;
  }
}

.navegacion-principal-link {
  color: #ffffff;
  display: block;
  text-align: center;
  transition: 0.3s;
}
.navegacion-principal-link:hover {
  color: #FEF46D;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 1024px) {
  .video video {
    width: 100%;
  }
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #9500ff;
  background: linear-gradient(259deg, rgba(149, 0, 255, 0.5) 0%, rgba(239, 0, 255, 0.3) 100%);
  display: flex;
  align-items: center;
}

.contenido-video {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 10;
}

.video-title {
  color: #ffffff;
}
@media (min-width: 768px) {
  .video-title {
    font-size: 7rem;
  }
}

.video-date {
  background-color: #F53756;
  align-self: flex-start;
  color: #ffffff;
  padding: 1rem 2rem;
  font-weight: 700;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}

.sobre-festival-title {
  margin-bottom: 2rem;
}

.sobre-festival-date {
  color: #F53756;
  font-weight: 700;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}

.lineup-dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

.escenario {
  padding-top: 3rem;
}

.escenario-nombre::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .escenario-nombre::after {
  background-image: url(../img/icono_edm.png);
}

.rock .escenario-nombre::after {
  background-image: url(../img/icono_rock.png);
}

.escenario-calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.escenario-calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.escenario-calendario li span {
  font-weight: 300;
}

.bg-amarillo {
  background-color: #FEF46D;
}
.bg-amarillo .escenario-calendario > li:nth-child(odd) {
  background-color: #fdec08;
}

.bg-verde {
  background-color: #4CB8B3;
}
.bg-verde .escenario-calendario > li:nth-child(odd) {
  background-color: #3b9691;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  margin-bottom: 5rem;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.galeria-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.7);
}
.galeria-overlay picture {
  display: block;
  max-width: 90%;
}
@media (min-width: 768px) {
  .galeria-overlay picture {
    max-width: 70%;
  }
}
@media (min-width: 1024px) {
  .galeria-overlay picture {
    max-width: 50%;
  }
}

.btn-cerrar {
  background-color: #ffffff;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
  font-size: 3rem;
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 5rem;
}

@media (min-width: 768px) {
  .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  color: #000000;
}
.pase-header p, .pase-footer p {
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 400;
  padding: 0.5rem 0;
}
.pase-header {
  background-color: #F53756;
}
.pase-header p {
  font-size: 1.6rem;
}
.pase-body {
  background-color: #ffffff;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li {
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752f97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 300;
}

.nombre-pase {
  text-align: center;
  font-size: 2.4rem;
  color: #ffffff;
  margin-bottom: 3rem;
}

.footer {
  background-color: #F53756;
  padding: 1rem 0;
}
.footer p {
  text-align: center;
  color: #ffffff;
}